import {
  Employee,
  Constraint,
  ScheduleSettings,
  OptimizedSchedule,
} from "@/types/schedule"

export interface ScheduleEvaluationContext {
  optimized: OptimizedSchedule
  employees: Employee[]
  constraints: Constraint[]
  settings: ScheduleSettings
  daysInMonth: number
  selectedMonth: number
  selectedYear: number
  activeSlots: number[]
  employeeShiftCounts: number[]
  employeeConsecutiveDays: Array<{ current: number; max: number }>
  employeeHolidayShifts: number[]
  employeeWeekdayShifts: number[]
  employeeWeekendConsecutiveDays: Array<{ current: number; max: number }>
}

export interface ConstraintEvaluator {
  name: string
  evaluate(context: ScheduleEvaluationContext): number
}

export class DuplicatePersonEvaluator implements ConstraintEvaluator {
  name = "duplicate-person"
  private readonly DUPLICATE_PERSON_IN_SHIFT_PENALTY = 2000

  evaluate(context: ScheduleEvaluationContext): number {
    let cost = 0
    const { activeSlots, settings, daysInMonth } = context
    const { shiftsPerDay, personsPerShift } = settings

    for (let day = 0; day < daysInMonth; day++) {
      const dayStartSlot = day * shiftsPerDay * personsPerShift
      for (let shift = 0; shift < shiftsPerDay; shift++) {
        const shiftStartSlot = dayStartSlot + shift * personsPerShift
        const shiftEndSlot = shiftStartSlot + personsPerShift

        const uniqueEmployees = new Set(
          activeSlots.slice(shiftStartSlot, shiftEndSlot)
        )

        if (uniqueEmployees.size < personsPerShift) {
          cost += this.DUPLICATE_PERSON_IN_SHIFT_PENALTY
        }
      }
    }

    return cost
  }
}

export class AvoidConstraintEvaluator implements ConstraintEvaluator {
  name = "avoid-constraint"
  private readonly AVOID_VIOLATION_PENALTY = 1000

  evaluate(context: ScheduleEvaluationContext): number {
    let cost = 0
    const { constraints, employees, activeSlots, settings, daysInMonth } =
      context
    const { shiftsPerDay, personsPerShift } = settings

    for (let day = 0; day < daysInMonth; day++) {
      const dayNumber = day + 1
      const dayStartSlot = day * shiftsPerDay * personsPerShift
      const dayEndSlot = dayStartSlot + shiftsPerDay * personsPerShift
      const assignedEmployeeIndices = activeSlots.slice(
        dayStartSlot,
        dayEndSlot
      )

      constraints.forEach((constraint) => {
        if (constraint.type === "avoid" && constraint.date === dayNumber) {
          const empIndex = employees.findIndex(
            (emp) => emp.id === constraint.employeeId
          )
          if (empIndex !== -1 && assignedEmployeeIndices.includes(empIndex)) {
            cost += this.AVOID_VIOLATION_PENALTY
          }
        }
      })
    }

    return cost
  }
}

export class PreferConstraintEvaluator implements ConstraintEvaluator {
  name = "prefer-constraint"
  private readonly MISSED_PREFERENCE_PENALTY = 10

  evaluate(context: ScheduleEvaluationContext): number {
    let cost = 0
    const { constraints, employees, activeSlots, settings, daysInMonth } =
      context
    const { shiftsPerDay, personsPerShift } = settings

    for (let day = 0; day < daysInMonth; day++) {
      const dayNumber = day + 1
      const dayStartSlot = day * shiftsPerDay * personsPerShift
      const dayEndSlot = dayStartSlot + shiftsPerDay * personsPerShift
      const assignedEmployeeIndices = activeSlots.slice(
        dayStartSlot,
        dayEndSlot
      )

      constraints.forEach((constraint) => {
        if (constraint.type === "prefer" && constraint.date === dayNumber) {
          const empIndex = employees.findIndex(
            (emp) => emp.id === constraint.employeeId
          )
          if (empIndex !== -1 && !assignedEmployeeIndices.includes(empIndex)) {
            cost += this.MISSED_PREFERENCE_PENALTY
          }
        }
      })
    }

    return cost
  }
}

export class ConsecutiveShiftsEvaluator implements ConstraintEvaluator {
  name = "consecutive-shifts"
  private readonly CONSECUTIVE_PENALTY = 100
  private readonly WEEKEND_CONSECUTIVE_PENALTY = 50

  evaluate(context: ScheduleEvaluationContext): number {
    let cost = 0
    const {
      employees,
      settings,
      employeeConsecutiveDays,
      employeeWeekendConsecutiveDays,
    } = context

    employees.forEach((emp, index) => {
      let penalty = 0

      if (employeeConsecutiveDays[index].max > settings.maxConsecutiveShifts) {
        const excess =
          employeeConsecutiveDays[index].max - settings.maxConsecutiveShifts
        penalty += excess * this.CONSECUTIVE_PENALTY
      }

      // Weekend type employees get bonus for consecutive weekend work
      if (emp.tags.includes("tags.weekendType")) {
        if (employeeWeekendConsecutiveDays[index].max >= 2) {
          penalty = 0 // Override penalty for good weekend consecutive work
        } else {
          penalty += this.WEEKEND_CONSECUTIVE_PENALTY
        }
      }

      cost += penalty
    })

    return cost
  }
}

export class WeeklyLimitsEvaluator implements ConstraintEvaluator {
  name = "weekly-limits"
  private readonly WEEKLY_LIMIT_PENALTY = 100
  private readonly REST_DAYS_PENALTY = 80

  evaluate(context: ScheduleEvaluationContext): number {
    let cost = 0
    const { employees, settings, daysInMonth, activeSlots } = context
    const { shiftsPerDay, personsPerShift } = settings

    for (let week = 0; week < Math.ceil(daysInMonth / 7); week++) {
      const weekStart = week * 7
      const weekEnd = Math.min(weekStart + 6, daysInMonth - 1)

      employees.forEach((emp, empIndex) => {
        let weeklyShifts = 0
        const workDays: number[] = []

        for (let day = weekStart; day <= weekEnd; day++) {
          const dayStartSlot = day * shiftsPerDay * personsPerShift
          const dayEndSlot = Math.min(
            (day + 1) * shiftsPerDay * personsPerShift,
            activeSlots.length
          )
          let workedThisDay = false

          for (
            let slotIndex = dayStartSlot;
            slotIndex < dayEndSlot;
            slotIndex++
          ) {
            if (activeSlots[slotIndex] === empIndex) {
              workedThisDay = true
              break
            }
          }

          if (workedThisDay) {
            weeklyShifts++
            workDays.push(day)
          }
        }

        // Weekly limits penalty
        if (weeklyShifts > settings.maxShiftsPerWeek) {
          cost +=
            (weeklyShifts - settings.maxShiftsPerWeek) *
            this.WEEKLY_LIMIT_PENALTY
        }

        // Rest days penalty
        for (let i = 0; i < workDays.length - 1; i++) {
          const daysBetween = workDays[i + 1] - workDays[i] - 1
          if (daysBetween < settings.minRestDaysBetweenShifts) {
            cost +=
              (settings.minRestDaysBetweenShifts - daysBetween) *
              this.REST_DAYS_PENALTY
          }
        }
      })
    }

    return cost
  }
}

export class EvenDistributionEvaluator implements ConstraintEvaluator {
  name = "even-distribution"
  private readonly UNEVEN_DISTRIBUTION_PENALTY = 50

  evaluate(context: ScheduleEvaluationContext): number {
    let cost = 0
    const { settings, employeeShiftCounts } = context

    if (settings.evenDistribution) {
      const avgShifts =
        employeeShiftCounts.reduce((sum, count) => sum + count, 0) /
        employeeShiftCounts.length
      const variance =
        employeeShiftCounts.reduce(
          (sum, count) => sum + Math.pow(count - avgShifts, 2),
          0
        ) / employeeShiftCounts.length
      cost += variance * this.UNEVEN_DISTRIBUTION_PENALTY
    }

    return cost
  }
}

export class HolidayDistributionEvaluator implements ConstraintEvaluator {
  name = "holiday-distribution"
  private readonly HOLIDAY_DISTRIBUTION_PENALTY = 1000

  evaluate(context: ScheduleEvaluationContext): number {
    let cost = 0
    const {
      employees,
      employeeShiftCounts,
      employeeHolidayShifts,
      employeeWeekdayShifts,
    } = context

    const totalHolidayShifts = employeeHolidayShifts.reduce(
      (sum, count) => sum + count,
      0
    )
    const totalWeekdayShifts = employeeWeekdayShifts.reduce(
      (sum, count) => sum + count,
      0
    )

    if (totalHolidayShifts > 0 && totalWeekdayShifts > 0) {
      employees.forEach((_, empIndex) => {
        const employeeTotalShifts = employeeShiftCounts[empIndex]
        if (employeeTotalShifts > 0) {
          const holidayRatio =
            employeeHolidayShifts[empIndex] / employeeTotalShifts
          const globalHolidayRatio =
            totalHolidayShifts / (totalHolidayShifts + totalWeekdayShifts)
          const holidayRatioDeviation = Math.abs(
            holidayRatio - globalHolidayRatio
          )

          // Special penalty for employees who only work holidays or only work weekdays
          if (
            employeeHolidayShifts[empIndex] > 0 &&
            employeeWeekdayShifts[empIndex] === 0 &&
            employeeTotalShifts > 1
          ) {
            cost += this.HOLIDAY_DISTRIBUTION_PENALTY * 2
          } else if (
            employeeWeekdayShifts[empIndex] > 0 &&
            employeeHolidayShifts[empIndex] === 0 &&
            employeeTotalShifts > 1
          ) {
            cost += this.HOLIDAY_DISTRIBUTION_PENALTY
          } else if (holidayRatioDeviation > 0.3) {
            cost += holidayRatioDeviation * this.HOLIDAY_DISTRIBUTION_PENALTY
          }
        }
      })
    }

    return cost
  }
}
