import {
  ConstraintEvaluator,
  DuplicatePersonEvaluator,
  AvoidConstraintEvaluator,
  PreferConstraintEvaluator,
  ConsecutiveShiftsEvaluator,
  WeeklyLimitsEvaluator,
  EvenDistributionEvaluator,
  HolidayDistributionEvaluator,
} from "./constraintEvaluators"
import { ScheduleMetricsCalculator } from "./metricsCalculator"
import {
  Employee,
  Constraint,
  ScheduleSettings,
  OptimizedSchedule,
} from "@/types/schedule"

export class ScheduleCostCalculator {
  private evaluators: ConstraintEvaluator[] = [
    new DuplicatePersonEvaluator(),
    new AvoidConstraintEvaluator(),
    new PreferConstraintEvaluator(),
    new ConsecutiveShiftsEvaluator(),
    new WeeklyLimitsEvaluator(),
    new EvenDistributionEvaluator(),
    new HolidayDistributionEvaluator(),
  ]

  addEvaluator(evaluator: ConstraintEvaluator): void {
    this.evaluators.push(evaluator)
  }

  removeEvaluator(name: string): void {
    this.evaluators = this.evaluators.filter((e) => e.name !== name)
  }

  getEvaluators(): ConstraintEvaluator[] {
    return [...this.evaluators]
  }

  calculate(
    optimized: OptimizedSchedule,
    employees: Employee[],
    constraints: Constraint[],
    settings: ScheduleSettings,
    daysInMonth: number,
    selectedMonth: number,
    selectedYear: number
  ): number {
    try {
      // Pre-compute metrics
      const context = ScheduleMetricsCalculator.calculateMetrics(
        optimized,
        employees,
        constraints,
        settings,
        daysInMonth,
        selectedMonth,
        selectedYear
      )

      // Evaluate all constraints
      let totalCost = 0
      for (const evaluator of this.evaluators) {
        const cost = evaluator.evaluate(context)
        totalCost += cost

        // Optional: log individual costs for debugging
        if (cost > 0) {
          console.log(`${evaluator.name}: ${cost}`)
        }
      }

      console.log(`Total cost calculated: ${totalCost}`)
      return totalCost
    } catch (error) {
      console.error("Error calculating cost:", error)
      throw error
    }
  }
}
